.toastBar {
    -webkit-animation: toastBar 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    animation: toastBar 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
    position: absolute;
    top: 0;
    left: calc(50% - 175px);
    background: white;
    color: black;
    width: 350px;
    padding: 25px;
    text-align: center;
}
@-webkit-keyframes toastBar {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
    }
    100% {
        -webkit-transform: translateY(75px);
        transform: translateY(75px);
    }
}
@keyframes toastBar {
    0% {
        -webkit-transform: translateY(0px);
        transform: translateY(0px);
    }
    100% {
        -webkit-transform: translateY(75px);
        transform: translateY(75px);
    }
}